[data-dark="false"] {
  --primary: #0f766e;
  --secondary: #e2f5f1;
  --text: #0f172a;
  --background: #f8fafc;
  --background-alt: #f1f5f9;
  --light-gray: #e2e8f0;
  --gray: #64748b;
  --dark-gray: #334155;
  --overlay: #0f172a0f;
  --surface: #ffffff;
  --surface-alt: #f8fafc;
  --surface-frost: #ffffff;
  --surface-veil: #ffffff;
  --border: #e2e8f0;
  --focus: #0f766e33;
  --primary-soft: #0f766e33;
}

[data-dark="true"] {
  --primary: #2dd4bf;
  --secondary: #15332e;
  --text: #e2e8f0;
  --background: #0b1220;
  --background-alt: #0f172a;
  --light-gray: #243244;
  --gray: #94a3b8;
  --dark-gray: #cbd5e1;
  --overlay: #0000004a;
  --surface: #111b2d;
  --surface-alt: #13203a;
  --surface-frost: #111b2d;
  --surface-veil: #111b2d;
  --border: #243244;
  --focus: #2dd4bf40;
  --primary-soft: #2dd4bf44;
}

:root {
  --title: "Plus Jakarta Sans", sans-serif;
  --heading: "Plus Jakarta Sans", sans-serif;
  --body: "Source Sans 3", sans-serif;
  --code: "JetBrains Mono", monospace;
  --large: 1.25rem;
  --xl: 1.55rem;
  --xxl: 2.2rem;
  --thin: 200;
  --regular: 400;
  --semi-bold: 600;
  --bold: 700;
  --spacing: 1.75;
  --compact: 1.45;
  --rounded-sm: 8px;
  --rounded: 12px;
  --rounded-lg: 16px;
  --shadow: 0 4px 16px -8px var(--overlay);
  --shadow-strong: 0 12px 28px -18px var(--overlay);
  --transition: 160ms ease;
  --page: 1080px;
  --section-pad: clamp(24px, 4vw, 56px);
}
